import { createSlice } from "@reduxjs/toolkit";
import { v4 as uuidv4 } from "uuid";

let initialState = [
  {
    id: 1,
    conversation: [
      {
        id: uuidv4(),
        contactid: 0,
        text: "Hi, Alice)",
        date: "01.10.2021",
        time: "4:10 AM",
      },
      {
        id: uuidv4(),
        contactid: 1,
        text: "Hello! So hows your learning going, Anna?",
        date: "01.10.2021",
        time: "4:11 AM",
      },
      {
        id: uuidv4(),
        contactid: 0,
        text: "Couldnt be better!Thanks for asking, Alice)",
        date: "01.10.2021",
        time: "4:15 AM",
      },
    ],
  },
  {
    id: 2,
    conversation: [
      {
        id: uuidv4(),
        contactid: 2,
        text: "Anna, have you finished the task?",
        date: "08.10.2021",
        time: "11:10 AM",
      },
      {
        id: uuidv4(),
        contactid: 0,
        text: "Yes, I have already created a pool request. He is waiting for approval)",
        date: "08.10.2021",
        time: "11:12 AM",
      },
      {
        id: uuidv4(),
        contactid: 2,
        text: "You are rock!",
        date: "08.10.2021",
        time: "11:13 AM",
      },
    ],
  },
];

const reduxState = JSON.parse(localStorage.getItem("reduxState"));

if (reduxState) initialState = reduxState.messages || initialState;

export const slice = createSlice({
  name: "messages",
  initialState: initialState,
  reducers: {
    addMessages: (state, action) => {
      state.push({ ...action.payload, id: uuidv4() });
      return state;
    },
  },
});

export const { addMessages } = slice.actions;

export const selectMessages = (state) => state.messages;

export default slice.reducer;
